@import url(https://fonts.googleapis.com/css?family=Exo+2:200i);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:white;
}

p{
  font-size: 14px;
  margin-left: 15px;
  font-family: 'Arial'
}

a{
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Cell {
    height: 80px;
    width: 80px;
    background-color: darkgray;
    cursor: pointer;
    border-radius: 15%;
  }
  
  .Cell-lit {
    background-color: white;
  }
.Board-table {
  margin: 50px auto;
}

:root {
  font-size: 12px;   
  --neon-text-color: #f40;
  --neon-border-color: #08f;
}

.Board-rulesContainer{
  width: 900px;
  text-align: center;
  margin: 0 auto;
  line-height: 2.5rem;
}

.Board-rules{
  font-family: 'Press Start 2P';
  font-size: 0.9rem;
}

h1{
  font-weight: 200;
  font-size: 4em;
  margin: 20px auto;
  font-style: italic;
  width: 300px;
  padding: 2rem 3rem 3.5rem;
  border: 0.4rem solid #fff;
  border-radius: 2rem;
  text-transform: uppercase;
  text-shadow: 
  .1vw 0vw .25vw #ffd8d8, .2vw 0vw .25vw #ffd8d8, .4vw 0vw .25vw #ffd8d8, 
  .1vw 0vw   0vw #f25757, .2vw 0vw   0vw #f25757, .4vw 0vw   0vw #f25757,
  .1vw 0vw  .1vw #f25757, .2vw 0vw  .1vw #f25757, .4vw 0vw  .1vw #f25757,
  .1vw 0vw   2vw #f25757, .2vw 0vw   2vw #f25757, .4vw 0vw   2vw #f25757, 
  .1vw 0vw   1vw #e50b0b, .2vw 0vw   1vw #e50b0b, .4vw 0vw   5vw #e50b0b, 
  .1vw 0vw   5vw #e50b0b, .2vw 0vw  20vw #e50b0b, .4vw 0vw  10vw #e50b0b,
  .1vw 0vw  10vw #e50b0b, .2vw 0vw  30vw #e50b0b, .4vw 0vw  10vw #e50b0b; 

  box-shadow:
  0 0 2rem #08f,
  inset 0 0 2rem #08f,
  0 0 4rem #08f,
  inset 0 0 4rem #08f; 

  box-shadow:
  0 0 2rem var(--neon-border-color),
  inset 0 0 2rem var(--neon-border-color),
  0 0 4rem var(--neon-border-color),
  inset 0 0 4rem var(--neon-border-color); 
}

h1::selection {
  background-color: #08f;
  background-color: var(--neon-border-color);
  color: #f40;
  color: var(--neon-text-color);
}

h1:focus {
  outline: none;
}

button{
  background-color: transparent;
  color: white;
  padding: 20px;
  font-family: 'Exo 2', sans-serif;
  font-weight: 200;
  font-size: 2.5rem;
  text-transform: uppercase;
  border: 0.4rem solid #fff;
  border-radius: 2rem;
  outline:none;
  text-shadow: 
  .1vw 0vw .25vw #ffd8d8, .2vw 0vw .25vw #ffd8d8, .4vw 0vw .25vw #ffd8d8, 
  .1vw 0vw   0vw #f25757, .2vw 0vw   0vw #f25757, .4vw 0vw   0vw #f25757,
  .1vw 0vw  .1vw #f25757, .2vw 0vw  .1vw #f25757, .4vw 0vw  .1vw #f25757,
  .1vw 0vw   2vw #f25757, .2vw 0vw   2vw #f25757, .4vw 0vw   2vw #f25757, 
  .1vw 0vw   1vw #e50b0b, .2vw 0vw   1vw #e50b0b, .4vw 0vw   5vw #e50b0b, 
  .1vw 0vw   5vw #e50b0b, .2vw 0vw  20vw #e50b0b, .4vw 0vw  10vw #e50b0b,
  .1vw 0vw  10vw #e50b0b, .2vw 0vw  30vw #e50b0b, .4vw 0vw  10vw #e50b0b; 

  box-shadow:
  0 0 2rem #08f,
  inset 0 0 2rem #08f,
  0 0 4rem #08f,
  inset 0 0 4rem #08f; 

  box-shadow:
  0 0 2rem var(--neon-border-color),
  inset 0 0 2rem var(--neon-border-color),
  0 0 4rem var(--neon-border-color),
  inset 0 0 4rem var(--neon-border-color); 
}

button:hover{
  cursor: pointer;
}
.App {
  text-align: center;
}

body{
  background-color: #000;
  font-family: 'Exo 2', sans-serif; 
}

